<style lang="less">
    .demo-Circle-custom{
        & h1{
            color: #3f414d;
            font-size: 28px;
            font-weight: normal;
        }
        & p{
            color: #657180;
            font-size: 14px;
            margin: 10px 0 15px;
        }
        & span{
            display: block;
            padding-top: 15px;
            color: #657180;
            font-size: 14px;
            &:before{
                content: '';
                display: block;
                width: 50px;
                height: 1px;
                margin: 0 auto;
                background: #e0e3e6;
                position: relative;
                top: -15px;
            };
        }
        & span i{
            font-style: normal;
            color: #3f414d;
        }
    }
</style>
<style scoped>
    .contents{
        text-align: left 
    }

</style>
<template>
    <div>
        <Tabs value="TaskList">
            <TabPane label="TaskList" name="TaskList" icon="logo-apple">
                <Scroll :height="contentHeight">
                <Collapse class="contents" v-model="value2" accordion>
                    <Panel v-for="item,index in tasks" :key="index"  style="white-space: wrap;font-size:15px;overflow: hidden;text-overflow: ellipsis"> 
                        <Badge status="processing" />{{item.title}}
                        <br>
                        <p slot="content" style="white-space: wrap;">{{item.description}}
                        <br> 
                        <br> 
                        <Badge status="warning" />{{item.createTime}}
                        </p>
                    </Panel>
                </Collapse>
                </Scroll>
            </TabPane>
            <TabPane label="TaskGraph" icon="logo-windows">    
                <div style="height: 100px;">
                    <Progress vertical :percent="25" />
                    <Progress vertical :percent="45" status="active" />
                    <Progress vertical :percent="65" status="wrong" />
                    <Progress vertical :percent="100" />
                    <Progress vertical :percent="25" hide-info />
                </div>
                <div style="height: 100px;">
                    <i-circle
                        :size="250"
                        :trail-width="4"
                        :stroke-width="5"
                        :percent="75"
                        stroke-linecap="square"
                        stroke-color="#43a3fb">
                        <div class="demo-Circle-custom">
                            <h1>{{this.num}}</h1>
                            <p></p>
                            <span>
                                
                                <i>{{this.num}}</i>
                            </span>
                        </div>
                    </i-circle>
                </div>
            </TabPane>
        </Tabs>
        <Row :style="{height:'25px'}">
            <i-col span="8" style="padding-top:4px">&nbsp;</i-col>
            <i-col span="8" style="padding-top:4px">&nbsp;</i-col>
            <i-col span="8" style="padding-top:4px"><i-button size="large" @click="holdBack" shape="circle" icon="ios-arrow-back"></i-button></i-col>

        </Row>
    </div>
</template>
<script>
import aplayer from "vue-aplayer";
export default {
  name: "Aplayer",
  props: ["pdfurl"],
  components: {
    //
    aplayer: aplayer
  },
  data () {
    return {
        value2: '1',
        tasks: [],
        num: 0,
        content: '',
        usercount: '',
 conheight:{
            height:'',
            overflow:'true'
        },
        contentHeight: 200
    }
  },
mounted() {
    this.conheight.height=window.innerHeight-130+"px";
    this.contentHeight = window.innerHeight-130;
    //window.innerHeightthis.$refs.table.$el.offsetTop
  },
  created() {
    this.usercount = this.$route.params.usercount;
    this.load();
    window.addEventListener('resize', this.setHeight);
    this.setHeight();
    // this.loadMusic();
  },

  methods: {
    setHeight(){
        this.conheight.height= window.innerHeight-130+"px";
        this.contentHeight = window.innerHeight-130;
    },

    async load() {
        
        let nowDate = this.getDate(0);

        let { data } = await this.$axios.get(this.$apiTypes.GET_TASKS);
        
        if(data != ''){

            this.tasks = data.data;
            this.num = data.num;

        }

        console.info(data);

    },
    holdBack(){
        this.$router.push({name:'main',params:{usercount: this.usercount}});
    },
    warning (nodesc) {
        this.$Notice.warning({
        title: 'Notification title',
            desc: nodesc ? '' : ' '
        });
    },
    getDate(n) {

        var ss = 24 * 60 * 60 * 1000; //86400

        var timestamp = new Date().getTime(); //

        var date1 = new Date(ss * n + timestamp) //n

        var newTime = date1.toLocaleString(); //2018/6/4 10:45:19 

        var arr = newTime.split(" "); //2018/6/4

        var arr2 = arr[0].split('/'); //

        return arr2[0] + '' + arr2[1] + '' + arr2[2] + ''+' '+ arr[1]; //

    }



    }
  }
</script>



